  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.btn-reset {
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer; }

@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0; }
  100% {
    visibility: hidden;
    opacity: 0; } }

.container {
  width: 100%;
  max-width: 1296px;
  margin: 0 auto;
  padding: 0 40px; }

.h1 {
  font-weight: bold;
  font-size: 24px;
  line-height: 150%;
  color: #343030; }

.h2 {
  font-weight: bold;
  font-size: 24px;
  line-height: 150%;
  color: #343030; }

.h3 {
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
  color: #343030; }

.header {
  width: 100%;
  min-height: 96px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e1e1e1; }

.header__logo {
  color: #776763; }

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 16px; }

.main-nav {
  margin-left: 48px;
  margin-right: 48px; }

.main-nav__list {
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0 0 10px; }

.main-nav__item {
  flex-shrink: 0; }
  .main-nav__item:not(:last-of-type) {
    margin-right: 49px; }

.main-nav__link {
  font-size: 14px;
  line-height: 150%;
  color: #343030;
  transition: opacity 0.3s ease; }
  @media (hover: hover), screen and (min-width: 0) {
    .main-nav__link:hover, .main-nav__link:focus {
      opacity: 0.5; } }

.footer {
  width: 100%;
  min-height: 96px;
  display: flex;
  align-items: center;
  background-color: #eceaea; }

.footer__logo {
  color: #403432; }

.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 16px; }

.footer__contacts {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: auto;
  margin-bottom: 8px; }

.footer__contacts-link {
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  line-height: 150%;
  color: #555555;
  transition: opacity 0.3s ease; }
  @media (hover: hover), screen and (min-width: 0) {
    .footer__contacts-link:hover, .footer__contacts-link:focus {
      opacity: 0.5; } }
  .footer__contacts-link:not(:last-of-type) {
    margin-right: 48px; }
  .footer__contacts-link svg {
    margin-right: 8px; }

.btn {
  cursor: pointer;
  padding: 0;
  border: none;
  outline: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 117px;
  height: 48px;
  background-color: #382e2b;
  color: #f4f6f9;
  font-weight: bold;
  font-size: 14px;
  line-height: 150%;
  text-align: center;
  transition: background-color 0.3s ease; }
  .btn span:not([class]) {
    display: inline-flex;
    align-items: center; }
  .btn.loading {
    pointer-events: none; }
    .btn.loading:hover, .btn.loading:focus {
      background-color: #382e2b; }
  .btn:hover, .btn:focus {
    background-color: #776763; }
  .btn--in-cart {
    background-color: #5b3a32; }
    .btn--in-cart svg {
      margin-right: 4px; }

.loader {
  font-size: 3px;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #ffffff;
  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  animation: load 1.4s infinite linear;
  transform: translateZ(0); }
  .loader:before {
    width: 50%;
    height: 50%;
    background: #ffffff;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: ''; }
  .loader:after {
    background: #382e2b;
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }

@keyframes load {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.gallery {
  padding-top: 45px;
  padding-bottom: 45px; }

.gallery__title {
  margin-top: 0;
  margin-bottom: 39px; }

.gallery__list {
  margin-top: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  width: calc(100% + 32px);
  margin-right: -32px;
  margin-bottom: -32px; }

.gallery__item {
  width: calc(25% - 32px);
  margin-right: 32px;
  margin-bottom: 32px;
  display: flex;
  flex-direction: column; }
  @media (max-width: 1279px) {
    .gallery__item {
      width: calc(33.33% - 32px); } }

.gallery-item {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  background-color: #f6f3f3;
  border: 1px solid #e1e1e1;
  transition: box-shadow 0.3s ease; }
  @media (hover: hover), screen and (min-width: 0) {
    .gallery-item:hover, .gallery-item:focus {
      box-shadow: 2px 2px 34px rgba(160, 172, 202, 0.2); } }
  .gallery-item--sold {
    opacity: 0.5;
    pointer-events: none; }

.gallery-item__img {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  height: 159px; }
  .gallery-item__img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; }

.gallery-item__content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 21px 24px 23px; }

.gallery-item__title {
  margin-top: 0;
  margin-bottom: 22px;
  font-size: 18px;
  line-height: 150%;
  color: #343030;
  letter-spacing: -0.02em; }

.gallery-item__footer {
  min-height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto; }

.gallery-item__price {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 12px 0 0; }

.gallery-item__old-price {
  font-weight: 300;
  font-size: 14px;
  line-height: 150%;
  text-decoration-line: line-through;
  color: #a0a0a0; }

.search-form {
  margin-left: auto;
  flex-grow: 1;
  max-width: 416px;
  margin-bottom: 7px; }
  .search-form form {
    display: flex;
    align-items: center; }
  .search-form button[type="submit"] {
    min-width: 122px; }

.input-wrapper {
  position: relative;
  flex-grow: 1; }
  .input-wrapper input,
  .input-wrapper textarea {
    width: 100%;
    font-size: 14px;
    line-height: 150%;
